  color: #130378 !important;
}

.scroll-more {
  color: white;
  text-align: center;
  width: 450px;
  position: absolute;
  bottom: 4rem;
  left: calc(50% - 225px);
  margin: 0 auto;
  font-size: 5rem;
  opacity: 1;
  transition: opacity 0.3s ease;
  font-family: monospace;
  pointer-events: none;
  z-index: -1;
  transform-origin: center;
  animation: bounce 3s cubic-bezier(0.37, 0, 0.21, 1.02) infinite, fonts 5s linear infinite;
}
@media only screen and (max-width: 37.5em) {
  .scroll-more {
    bottom: 4.5rem;
  }
}

.pull-in {
  font-size: 4rem;
  color: #130378;
  animation-name: fadeUpSlow;
  animation-duration: 8s;
}
@media only screen and (max-width: 37.5em) {
  .pull-in {
    font-size: 3rem;
  }
}
.pull-in .char {
  line-height: 1;
  transform-origin: center bottom;
  animation-name: pull-char;
  animation-timing-function: cubic-bezier(0.77, 0.02, 0.11, 0.97);
  animation-iteration-count: 11;
  animation-fill-mode: forwards;
  animation-delay: calc(0.05s * var(--char-index));
  animation-duration: calc(0.2s + 0.03s * var(--char-total));
}

@keyframes pull-char {
  20% {
    transition: transform 1s cubic-bezier(0.8, 0, 0.2, 1);
    transition-delay: calc(0.2s * (1 - var(--distance-percent)));
  }
  70% {
    transition-delay: calc(0.2s * var(--distance-percent));
    transform: scale(0.5) translateX(calc(-1.5em * var(--distance-sine)));
  }
}
/* ---------------------------------- */
.bounce-it {
  color: #130378;
  font-size: 3rem;
  animation-name: fadeUpSlow;
  animation-duration: 10s;
}
.bounce-it .char {
  line-height: 1;
  transform-origin: center bottom;
  animation-name: bounce-char;
  animation-timing-function: cubic-bezier(0.77, 0.02, 0.11, 0.97);
  animation-iteration-count: 17;
  animation-fill-mode: forwards;
  animation-delay: calc(0.05s * var(--char-index));
  animation-duration: calc(0.2s + 0.03s * var(--char-total));
}
.bounce-it:hover .char {
  animation-name: bounce-char;
}

@keyframes bounce-end {
  to {
    transform: translateY(0%) scale(1);
  }
}
@keyframes bounce-char {
  20% {
    transform: translateY(0%) scale(1.3, 0.8);
  }
  70% {
    transform: translateY(-40%) scale(0.8, 1.2);
  }
}
