  -o-filter: grayscale(100%);
}
.project-section .tiles img:hover {
  opacity: 1;
  -webkit-filter: grayscale(0%);
  -moz-filter: grayscale(0%);
  -o-filter: grayscale(0%);
}
@media (min-width: 768px) {
  .project-section .tiles {
    display: block;
  }
}
.project-section .middle {
  background: slategray;
  z-index: 2;
  padding: 0;
}
.project-section .middle img {
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background: #2a2a2a;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 3000;
  opacity: 0.9;
}

.nav-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  padding-right: 6.5rem;
}
@media only screen and (max-width: 775px) {
  .nav-menu {
    padding-right: 0;
  }
}

.navbar {
  display: flex;
  justify-content: space-between !important;
  align-items: center;
}
.navbar__text {
  color: #f2d375 !important;
  font-family: raleway, sans-serif;
  font-size: 1.5rem;
  padding: 0 1.5rem;
}
.navbar__text:hover, .navbar__text:active {
  color: #fff !important;
}
.navbar__text--home {
  display: visible;
  padding: 0 0 0 1.5rem;
}
@media only screen and (max-width: 775px) {
  .navbar__text--home {
    display: none;
  }
}

.nav-item {
  margin: 1rem 0;
}

.hamburger {
  display: none;
  position: absolute;
  top: 1rem;
  left: 1.5rem;
}

.bar {
  display: block;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  transition: all 0.3s ease-in-out;
  background-color: #713814;
}

@media only screen and (max-width: 776px) {
  .nav-menu {
    position: fixed;
    left: -100%;
    top: 3rem;
    flex-direction: column;
    background-color: #000;
    width: 100%;
    border-radius: 10px;
    text-align: center;
    transition: 0.3s;
    padding: 3rem 0;
    z-index: 500;
  }
  .nav-menu.active {
    left: 0%;
  }
  .hamburger {
    display: block;
    cursor: pointer;
  }
  .hamburger.active .bar:nth-child(2) {
    opacity: 0;
  }
  .hamburger.active .bar:nth-child(1) {
    transform: translateY(8px) rotate(45deg);
  }
  .hamburger.active .bar:nth-child(3) {
    transform: translateY(-8px) rotate(-45deg);
  }
}
.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 0.5rem;
  margin-left: 3.5rem;
}
.grid__column {
  display: flex;
  flex-direction: column;
}
.grid__title {
  font-size: 1.7rem;
  text-align: left;
  border-bottom: 1px solid #2a2a2a;
  padding: 0.5rem;
  margin: 0 0.5rem 1rem 0;
}
.grid__title--tools {
  width: 40%;
}
.grid__title--frameworks {
  width: 85%;
}
.grid__title--languages {
  width: 75%;
}
.grid__item {
  font-size: 1.2rem;
  width: 12rem;
  display: flex;
  flex-direction: row;
  margin: 1rem 0.5rem 0 0.5rem;
  align-items: center;
}
.grid img {
  width: 2.5rem;
  margin-right: 0.5rem;
}
@media only screen and (max-width: 56.25em) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media only screen and (max-width: 37.5em) {
  .grid {
    grid-template-columns: 1fr;
  }
}

.footer {
